<template>
  <div id="container">
    <el-menu  class="el-menu-demo" mode="horizontal" >
      <router-link to="/home" class="extend"><el-menu-item >首页</el-menu-item></router-link>
      <router-link to="/write" class="extend"><el-menu-item >写文章</el-menu-item></router-link>
      <router-link to="/login" class="extend" v-if="!login"><el-menu-item  >登录</el-menu-item></router-link>
      <router-link to="/userinfo/basic" class="extend"  v-else><el-menu-item >个人中心</el-menu-item></router-link>
      <router-link to="/register" class="extend"><el-menu-item>注册</el-menu-item></router-link>
      <el-input
        placeholder="请输入内容"
        prefix-icon="el-icon-search"
        class="search-input"
        v-model="searchInfo"
        @keyup.enter.native="search">
      </el-input>
      <p class="logo">记录生活,记录美
        <i class="el-icon-cold-drink"></i>
      </p>
    </el-menu>
  </div>
</template>

<script>
  import '@/assets/font/home.css'
  export default {
    name: "Header",
    data() {
      return {
        searchInfo: '',
        activeIndex:'1',
      }
    },
    computed: {
      login(){
        return this.$store.getters.getLoginState
      }
    },
    methods: {
      //执行搜索操作
      search(){
        this.$axios.get('/blog/api/search?searchInfo='+this.searchInfo).then((res) => {
          this.$store.dispatch('changeArticles', res.data)
          this.$store.dispatch('changeIfSearch', true)
        })
      }
    }
  }
</script>

<style scoped>
  #container {
    width: 100%;
    margin: 0 auto;
  }

  .logo {
    font-family: HOME;
    font-size: 28px;
    float: right;
    margin: 0;
    height: 60px;
    line-height: 60px;
    color: #409EFF;
  }

  .search-input {
    width: 150px;
    height: 40px;
    margin-top: 10px;
    margin-left: 20px;
  }

  a{
    display: inline-block;
    text-decoration: none;
    height: 60px;
  }

  .router-link-active{
    border-bottom: 2px solid #409EFF;
  }

  el-menu-item{
    color: black;
  }

</style>
