export default {
  userLogin: (context,payload) => {
    context.commit('userLogin',payload)
  },
  userLogout: (context) => {
    context.commit('userLogout')
  },
  changeArticles: (context, payload) => {
    context.commit('changeArticles',payload)
  },
  changeIfSearch: (context,payload) => {
    context.commit('changeIfSearch', payload)
  }
}
