export default {
  userLogin: (state,payload) => {
    state.login = true
    state.cur_user = payload
  },
  userLogout: state => {
    state.login = false
    state.cur_user = ''
  },
  changeArticles: (state, payload) =>{
    state.articles = payload
  },
  changeIfSearch: (state, payload) => {
    state.ifSearch = payload
  }
}
