<template>
  <div id="main">
    <Header/>
    <router-view/>
  </div>
</template>

<script>
  import Header from '@/components/Header'

  export default {
    name: 'app',
    components: {
      Header
    },
    created() {
      //初始化用户信息
      let loginState = localStorage.getItem('userLogin')
      if(loginState == "true"){
        let cur_user = JSON.parse(localStorage.getItem('cur_user'))
        this.$axios.get('/blog/changeSession?username='+cur_user.username).then((res) => {
          this.$store.dispatch('userLogin',cur_user)
        })
      }
    }
  }
</script>

<style>
  #main{
    width: 80%;
    margin: 0 auto;
  }

</style>
