import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

const router = new Router({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/home',
      component: () => import('@/views/Home')
    },
    {
      path: '/login',
      component: () => import('@/views/Login')
    },
    {
      path: '/register',
      component: () => import('@/views/Register')
    },
    {
      path: '/write',
      component: () => import('@/views/Write')
    },
    {
      path: '/detail/',
      component: () => import('@/views/Detail')
    },
    {
      path: '/userinfo',
      component: () => import('@/views/UserInfo'),
      children: [
        {
          path: '/userinfo/basic',
          component: () => import('@/views/UserInfo/basic')
        },
        {
          path: '/userinfo/article',
          component: () => import('@/views/UserInfo/article')
        },
        {
          path: '/userinfo/collection',
          component: () => import('@/views/UserInfo/collection')
        },
        {
          path: '/userinfo/deleteUser',
          component: () => import('@/views/UserInfo/deleteUser')
        },
        {
          path: '*',
          redirect:'/userinfo/basic'
        }
      ],
    },
    {
      path: '*',
      redirect:'/home'
    }
  ]

})

router.beforeEach((to, from, next) => {
  let login = localStorage.getItem("userLogin")
  let flag = (to.path=="/write" || to.path=="/userinfo" || to.path=="/userinfo/basic" || to.path=="/userinfo/article" || to.path=="/userinfo/collection" || to.path=="/userinfo/deleteUser") && (login=="false"||login==null)
  if(flag){
    next('/login')
  }else {
    next()

  }
})

export default router
